import React from "react";

import styled from "styled-components";

import { FaCheck, FaTimes } from "react-icons/fa";

const LabWorkContainer = styled.div`
  border-radius: 5px; /*      */

  padding: 10px; /*  */

  display: flex; /*  flex    */

  align-items: center; /*    */

  justify-content: space-between; /*      */

  margin: 10px 0; /*     */
`;

const StatusIndicator = styled.div`
  font-size: 30px; /*    */

  margin-right: 10px; /*      */

  color: ${({ passed }) => (passed ? "#21B200" : "#EA9090")}; /*   */
`;

const Grade = styled.span`
  font-size: 18px; /*    */

  font-weight: bold; /*   */

  margin: 0 10px; /*     */

  background-color: #d9d9d9; /*     */

  padding: 10px 20px; /*      */

  border-radius: 5px; /*    */

  min-width: 80px; /*    */

  text-align: center; /*     */

  color: ${({ passed }) =>
    passed ? "#21B200" : "#EA9090"}; /*       */
`;

const LabWorkItem = ({ score, passed }) => {
  return (
    <LabWorkContainer>
      <StatusIndicator passed={passed}>
        {passed ? <FaCheck /> : <FaTimes />} {/*    */}
      </StatusIndicator>
      <Grade passed={passed}>{score}/100</Grade>{" "}
      {/*     X/100 */}
    </LabWorkContainer>
  );
};

export default LabWorkItem;
