// LabItem.jsx
import React from "react";
import styled from "styled-components";

//    :    
const LabItemWrapper = styled.div`
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
  gap: 50px;
`;

//     
const LabNameContainer = styled.div`
  background-color: #ffffff;
  border-radius: 6px;
  padding: 40px 20px;
  flex: 1;
  margin-right: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const LabName = styled.div`
  font-weight: 600;
  font-size: 16px;
  color: #333;
`;

//    ,      
const LabGradeContainer = styled.div`
  flex: 1;
  border-radius: 6px;
  padding: 40px 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  
  background-color: ${({ grade }) => {
    switch (grade) {
      case "":
        return "#85A4518C";
      case "":
        return "#FF7070"; // - 
      default:
        return "#D9D9D9"; //   (, " ")
    }
  }};
  
  color: #fff;
  font-weight: 600;
  font-size: 16px;
  text-align: center;
`;

export default function LabItem({ name, grade }) {
  return (
    <LabItemWrapper>
      <LabNameContainer>
        <LabName>{name}</LabName>
      </LabNameContainer>
      <LabGradeContainer grade={grade}>
        : {grade}
      </LabGradeContainer>
    </LabItemWrapper>
  );
}
